import React, { useEffect, useRef, useState } from "react";
import "./style.css";
import { User } from "types/interface";
import defaultProfileImage from 'assets/image/default-profile-image.png';
import ExampleProfileImage from 'assets/image/default-profile-image_kirby.png'
import { useParams } from "react-router-dom";

// component:   
export default function UserPage() {

    // state: user email  //
    const {userEmail} = useParams();


    // component:    
    const UserTop = ()=> {

        // state:   input ref  //
        const ImageInputRef = useRef<HTMLInputElement|null>(null);
        // state:    //
        const [isMyPage, setIsMyPage] = useState<boolean>(false);
        // state:     //
        const [isChangeNickname, setIsChangeNickname] = useState<boolean>(true);
        // state:   //
        const [nickname, setNickname] = useState<string>('');
        // state:    //
        const [changeNickname, setChangeNickname] = useState<string>('');
        // state:    //
        const [profileImage, setProfileImage] = useState<string|null>(null);


        // effect: email path variable     //
        useEffect(()=>{
            if (!userEmail) return;
            setNickname('nickname');
            setProfileImage(ExampleProfileImage);
            
        },[userEmail])

        // render:     //
        return(
            <div id='user-top-wrapper'>
                <div className="user-top-container">
                    {isMyPage ? 
                    <div className="user-top-my-profile-image-box">
                        {profileImage !== null ?
                        <div className="user-top-profile-image" style={{backgroundImage: `url(${profileImage})`}}></div>
                        : <div className="user-top-my-profile-image-nothing-box">
                            <div className="icon-box-large">
                                <div className="icon image-box-white-icon"></div>
                            </div>
                        </div>
                        }

                        <input ref={ImageInputRef} type='file' accept="image/*" style={{display:'none'}}></input>


                    </div>
                    : <div className="user-top-profile-image-box" style={{backgroundImage: `url(${profileImage})`}}></div>
                    }
                    <div className="user-top-info-box">
                        <div className="user-top-info-nickname-box">
                            {isMyPage ? 
                                <>
                                {isChangeNickname ?
                                <input className="user-top-info-nickname-input"   type='text' size={nickname.length + 1} value={nickname}/>
                                :<div className="user-top-info-nickname">{nickname}</div>
                                }

                                <div className="icon-button">
                                    <div className="icon edit-icon"></div>
                                </div>
                                </> 
                                : <div className="user-top-info-nickname">{nickname}</div>
                            }
                            
                        </div>
                        <div className="user-top-info-email">{'email'}</div>
                    </div>
                </div>
            </div>
        );
    };

    // component:    
    const UserBottom = () => {
        // render:     
        return (
            <div id="user-bottom-wrapper">
                <div className="user-bottom-container">
                </div>
            </div>
        );
    };
    
    // render:    
    return (
    <div >
        <UserTop/>
        <UserBottom/>

    </div>)
}